import React from 'react'
import './style.css'

import Split from 'react-split'

import NetworkView from './NetworkView'
import NetworkList from './NetworkList'

import { camelCaseToTitleCase } from '../TableBrowser/camel-case-util'

import ListItemIcon from '@material-ui/core/ListItemIcon'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'
import { ListItem } from '@material-ui/core'

const titleStyle = {
  lineHeight: '22px',
  wordBreak: 'break-word'
}

const subtitleStyle = {
  height: '22px',
  lineHeight: '22px',
  wordBreak: 'break-word'
}

const infoStyle = {
  display: 'block',
  height: '22px',
  margin: '0',
  padding: '0'
}

/**
 * Top page for the application
 *
 * @param props
 * @returns {*}
 * @constructor
 */
const Ndex = props => {
  const handleFetch = (
    networkUUID,
    networkName,
    nodeCount,
    edgeCount,
    hitGenes
  ) => {
    //checkCytoscapeConnection(props)
    const geneList = props.search.queryList
    const sourceUUID = props.sourceUUID
    const id = props.search.results.jobId

    // Reset selection
    props.searchActions.clearSelectedGenes()
    props.uiStateActions.setHighlights(true)
    props.networkActions.networkFetchStarted({
      id,
      sourceUUID,
      networkUUID,
      networkName,
      geneList,
      hitGenes,
      nodeCount,
      edgeCount
    })
    updateHistory(networkUUID)
  }

  const updateHistory = networkUUID => {
    // Update URL
    const jobId = props.search.results.jobId
    const searchResults = props.search.searchResults
    const sourceName = props.uiState.selectedSource

    if (searchResults !== undefined && searchResults !== null) {
      console.log('** network changed:', jobId, sourceName)
      props.history.push(`/${jobId}/${sourceName}/${networkUUID}`)
    }
  }

  const handleImportNetwork = () => {
    // Reset the UI state (hilight)
    props.uiStateActions.setHighlights(true)

    props.cyrestActions.importNetworkStarted({
      cx: props.network.originalCX,
      source: props.network.sourceId,
      uuid: props.network.uuid
    })
  }

  const renderNetworkListItem = (
    querySize,
    networkEntry,
    classes,
    handleListItemClick,
    selectedIndex,
    index
  ) => {
    const {
      description,
      networkUUID,
      nodes,
      edges,
      imageURL,
      hitGenes,
      details
    } = networkEntry

    const genes = (
      <Tooltip title="Gene overlap" placement="left">
        <span style={infoStyle}>
          <Typography
            display="inline"
            color={
              props.uiState.selectedSource === 'enrichment' &&
              props.uiState.sortBy !== 'Overlap'
                ? 'textSecondary'
                : 'textPrimary'
            }
          >
            <strong>{hitGenes.length}</strong>{' '}
          </Typography>
          <Typography
            variant="caption"
            display="inline"
            color={
              props.uiState.selectedSource === 'enrichment' &&
              props.uiState.sortBy !== 'Overlap'
                ? 'textSecondary'
                : 'textPrimary'
            }
          >
            genes
          </Typography>
        </span>
      </Tooltip>
    )

    const icon = (
      <ListItemIcon style={{ width: '20px' }}>
        <img className="list-icon" src={imageURL} alt="list icon" />
      </ListItemIcon>
    )

    if (props.uiState.selectedSource === 'enrichment') {
      let pVal = details.PValue
      if (pVal !== undefined) {
        if (pVal < 1e-15) {
          pVal = '< 1e-15'
        } else {
          pVal = pVal.toExponential(2)
        }
      }

      const pv = (
        <Tooltip title="p-value" placement="left">
          <span style={infoStyle}>
            <Typography
              display="inline"
              color={
                props.uiState.sortBy === 'p-Value'
                  ? 'textPrimary'
                  : 'textSecondary'
              }
            >
              <strong>{pVal} </strong>
            </Typography>
            <Typography
              variant="caption"
              display="inline"
              color={
                props.uiState.sortBy === 'p-Value'
                  ? 'textPrimary'
                  : 'textSecondary'
              }
            >
              pv
            </Typography>
          </span>
        </Tooltip>
      )

      let similarity
      let sim = details.similarity
      if (sim !== undefined) {
        sim = sim.toFixed(2)
        similarity = (
          <Tooltip title="Cosine similarity" placement="left">
            <span style={infoStyle}>
              <Typography
                style={{}}
                display="inline"
                color={
                  props.uiState.sortBy === 'Similarity'
                    ? 'textPrimary'
                    : 'textSecondary'
                }
              >
                <strong>{sim} </strong>
              </Typography>
              <Typography
                variant="caption"
                display="inline"
                style={{}}
                color={
                  props.uiState.sortBy === 'Similarity'
                    ? 'textPrimary'
                    : 'textSecondary'
                }
              >
                similarity
              </Typography>
            </span>
          </Tooltip>
        )
      } else {
        similarity = null
      }

      const title = (
        <Typography style={titleStyle}>
          {description.split(':').slice(1)}
        </Typography>
      )

      const subtitle = (
        <span style={subtitleStyle}>
          <Typography variant="caption" color="textSecondary">
            Nodes: {nodes}, Edges: {edges}, Source:{' '}
            {camelCaseToTitleCase(description.split(':')[0])}
          </Typography>
        </span>
      )

      const newline = <Typography>{'\n'}</Typography>

      return (
        <ListItem
          button
          key={networkUUID}
          onClick={event => {
            handleFetch(networkUUID, description, nodes, edges, hitGenes)
            handleListItemClick(event, index)
          }}
          selected={selectedIndex === index}
        >
          <table
            style={{
              tableLayout: 'fixed',
              wordBreak: 'breakWord',
              borderCollapse: 'collapse',
              borderSpacing: '0'
            }}
          >
            <tbody>
              <tr padding="0">
                <td align="center" valign="middle" rowSpan="2" padding="0">
                  {icon}
                </td>
                <td align="left" valign="baseline" width="100px" padding="0">
                  {genes}
                  {newline}
                  {pv}
                  {newline}
                  {similarity}
                </td>
                <td align="left" valign="baseline" padding="0">
                  {title}
                  {newline}
                  {subtitle}
                </td>
              </tr>
            </tbody>
          </table>
        </ListItem>
      )
    } else {
      const node = (
        <div display="inline">
          <Typography display="inline" style={{}}>
            <strong>{nodes}</strong>{' '}
          </Typography>
          <Typography
            variant="caption"
            display="inline"
            color="textSecondary"
            style={{}}
          >
            {'nodes '}
          </Typography>
        </div>
      )

      const edge = (
        <div display="inline">
          <Typography display="inline" style={{}}>
            <strong>{edges}</strong>{' '}
          </Typography>
          <Typography
            variant="caption"
            display="inline"
            color="textSecondary"
            style={{}}
          >
            {'edges'}
          </Typography>
        </div>
      )

      const title = (
        <React.Fragment>
          <Typography color="textPrimary" style={titleStyle}>
            {description}
          </Typography>
        </React.Fragment>
      )

      const subtitle = (
        <Typography
          variant="caption"
          color="textSecondary"
          style={subtitleStyle}
        >
          Parent: {details.parent_network_nodes} nodes,{' '}
          {details.parent_network_edges} edges
        </Typography>
      )

      return (
        <ListItem
          button
          key={networkUUID}
          onClick={event => {
            handleFetch(networkUUID, description, nodes, edges, hitGenes)
            handleListItemClick(event, index)
          }}
          selected={selectedIndex === index}
        >
          <table
            style={{
              tableLayout: 'fixed',
              wordBreak: 'break-word'
            }}
          >
            <tbody>
              <tr height="50%">
                <td rowSpan="2" align="center" valign="middle" width="50px">
                  {icon}
                </td>
                <td colSpan="2" align="left" valign="bottom" width="150px">
                  {genes}
                </td>
                <td align="left" valign="bottom">
                  {title}
                </td>
              </tr>
              <tr>
                <td align="left" valign="baseline" width="75px">
                  {node}
                </td>
                <td align="left" valign="baseline" width="80px">
                  {edge}
                </td>
                <td align="left" valign="baseline">
                  {subtitle}
                </td>
              </tr>
            </tbody>
          </table>
        </ListItem>
      )
    }
  }

  return (
    <Split sizes={[40, 60]} gutterSize={7} className="ndex-base">
      <NetworkList
        renderNetworkListItem={renderNetworkListItem}
        handleFetch={handleFetch}
        hits={props.hits}
        {...props}
      />
      <NetworkView handleImportNetwork={handleImportNetwork} {...props} />
    </Split>
  )
}

export default Ndex
